                    d="M21.6324 47.9691C33.0274 47.9691 42.2648 38.7316 42.2648 27.3366C42.2648 15.9417 33.0274 6.70422 21.6324 6.70422C10.2374 6.70422 1 15.9417 1 27.3366C1 38.7316 10.2374 47.9691 21.6324 47.9691Z"
